import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-progress-bar.scss';
import {isNil, isNaN, isFinite, toNumber} from 'lodash';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import {configAriaSchema} from '@servicenow/library-schemas';

const _getProgress = (value, max) => {
	if (_isIndeterminate(value)) {
		return undefined;
	} else if (value < 0) {
		return 0;
	} else if (value > max) {
		return 100;
	} else if (value <= max) {
		return (value * 100) / max;
	}
};

const _isIndeterminate = (value) => {
	if (isNaN(toNumber(value)) || isNil(toNumber(value)) || !isFinite(value)) {
		return true;
	}
	return false;
};

/** @seismicView now-progress-bar */
const view = (state) => {
	const {pathType, size, value, max, configAria} = state.properties;

	return (
		<div
			class={{
				'now-progress-bar': true,
				['-' + size]: size
			}}>
			<div
				role="progressbar"
				class={{
					'now-progress-bar-path': true,
					['-' + pathType]: pathType,
					'-indeterminate': _isIndeterminate(value)
				}}
				{...filterAriaAttributes(configAria, 'progressbar')}
				aria-valuemin={_isIndeterminate(value) ? undefined : 0}
				aria-valuemax={_isIndeterminate(value) ? undefined : max}
				aria-valuenow={_isIndeterminate(value) ? undefined : value}
				style={{width: _getProgress(value, max) + `%`}}
			/>
		</div>
	);
};

/**
 * A linear indicator that provides feedback about an ongoing, user-initiated process.
 * Progress bar can represent a process that DOC1024234is loading or in progress.
 * When it represents progress, an indicator shows how much the user has completed out of the total.
 *
 * ```jsx
 * <now-progress-bar />
 * <now-progress-bar path-type="alert" size="lg" value="0.25" />
 * ```
 *
 * @seismicElement now-progress-bar
 * @summary A progress bar displays a path showing the completion or loading progress of an ongoing, user-initiated process.
 * @uib.label Progress bar
 * @uib.icon progress-bar
 * @uib.description Horizontal bar that fills with color to show progress
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties size, pathType, max, value, configAria
 */
createEnhancedElement('now-progress-bar', {
	properties: {
		/**
		 * Sets the progress bar styles. This indicates display progress by
		 * animating a bar along the length of a fixed, visible path.
		 * @type {('initial'|'alert'|'error'|'inactive')}
		 * @uib.label Style
		 * @uib.description Sets the progress bar style.
		 */
		pathType: {
			default: 'initial',
			schema: {type: 'string', enum: ['initial', 'alert', 'error', 'inactive']}
		},
		/**
		 * Describes how much work the task indicated by the
		 * progress bar requires. If present, must have a value greater than zero
		 * and be a valid floating point number.
		 * Default value: 1.
		 * @type {number}
		 * @uib.label Max. value
		 * @uib.description Sets the value that the progress bar must reach in order to be complete. If present, must have a value greater than zero and be a valid floating point number.
		 */
		max: {default: 1, schema: {type: 'number', minimum: 0}},
		/**
		 * Specifies how much of the represented task is complete.
		 * If the `max` property is provided, must be a valid floating point number between `0` and the value of the `max` property.
		 * If the `max` property is omitted, must be a valid floating point number between `0` and `1`.
		 *
		 * If a valid value (one that can be coerced to a number) is provided,
		 * then a determinate progress bar displays.
		 * If no value is provided, or an invalid value is provided, then an
		 * indeterminate progress bar displays.
		 * @type {number}
		 * @uib.label Value
		 * @uib.description Specifies how much of the represented task is complete. If the `max` property is provided, must be a valid floating point number between `0` and the value of the `max` property. If the `max` property is omitted, must be a valid floating point number between `0` and `1`. If a valid value (one that can be coerced to a number) is provided, then a determinate progress bar displays. If no value is provided, or an invalid value is provided, then an indeterminate progress bar displays.
		 * @uib.defaultValue 0.25
		 */
		value: {schema: {type: 'number', minimum: 0}},
		/**
		 * Sets the height of the progress bar track.
		 * @type {('xs'|'sm'|'md'|'lg')}
		 * @uib.description Sets the height of the progress bar track.
		 */
		size: {
			default: 'md',
			schema: {type: 'string', enum: ['xs', 'sm', 'md', 'lg']}
		},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-progress-bar. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element. The 'progressbar'
		 * key corresponds to the inner html `<div>` with a role of 'progressbar'.
		 *
		 * @type {{ 'aria-*': string }}
		 * @uib.label ARIA properties
		 * @uib.description ARIA properties that are set on the inner html. See https://www.w3.org/TR/wai-aria-1.1/#progressbar for properties and accepted values.
		 * @uib.defaultValue {}
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							progressbar: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		}
	},
	view,
	styles
});
